.td-wrap,
.td-wrap * {
    margin: 0;
    padding: 0;
    list-style: none;
    -webkit-box-sizing: initial !important;
    -moz-box-sizing: initial !important;
    box-sizing: initial !important;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.td-wrap svg {
    width: 100%;
}
.td-input {
    cursor: pointer;
}
.td-wrap {
    display: none;
    font-family: sans-serif;
    position: absolute;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
    outline: none;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 9999;
    color: #4d4d4d;
}
.td-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.td-clock {
    width: 156px;
    height: 156px;
    border-radius: 156px;
    box-shadow: 0 0 0 1px #1977CC, 0 0 0 8px rgba(0, 0, 0, 0.05);
    position: relative;
    background: #FFF;
    margin: 0 auto;
    text-align: center;
    line-height: 156px;
    position: absolute;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.td-clock:before {
    position: absolute;
    content: "";
    top: -10px;
    margin-left: -10px;
    left: 50%;
    width: 20px;
    height: 20px;
    transform: rotate(45deg);
    background: #FFF;
    border-left: 1px solid #1977CC;
    border-top: 1px solid #1977CC;
    border-top-left-radius: 4px;
}
.td-init .td-deg {
    -webkit-animation: slide 1s cubic-bezier(.7, 0, .175, 1) 1.2s infinite;
}
.td-svg {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}
.td-svg-2 {
    position: absolute;
    top: 18px;
    left: 18px;
    bottom: 18px;
    right: 18px;
}
.td-wrap.td-show {
    display: block;
}
.td-deg {
    background-position: center;
    background-repeat: no-repeat;
    background-size: 80%;
    position: absolute;
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.td-medirian {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.td-medirian span {
    width: 40px;
    height: 40px;
    border-radius: 40px;
    line-height: 40px;
    text-align: center;
    margin: 0;
    position: absolute;
    z-index: 2;
    left: 50%;
    margin-left: -20px;
    font-size: .8em;
    opacity: 0;
    font-weight: bold;
}
.td-medirian .td-icon-am {
    top: 40px;
}
.td-medirian .td-icon-pm {
    bottom: 40px;
}
.td-medirian .td-icon-am.td-on {
    top: 26px;
    opacity: 1;
}
.td-medirian .td-icon-pm.td-on {
    bottom: 26px;
    opacity: 1;
}
.td-select {
    position: absolute;
    top: 4px;
    left: 32px;
    right: 32px;
    bottom: 22px;
}
.td-select svg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}
.td-select:after {
    position: absolute;
    background: #FFF;
    width: 36px;
    height: 36px;
    box-shadow: 0 0 0 1px #1977CC;
    border-radius: 60px;
    top: -16px;
    left: 50%;
    margin-left: -18px;
    border-bottom-left-radius: 0;
    content: "";
    transform: rotate(-45deg);
    cursor: pointer;
}
.td-clock .td-time {
    font-weight: bold;
    position: relative;
}
.td-clock .td-time span {
    width: 42px;
    height: 42px;
    display: inline-block;
    vertical-align: middle;
    line-height: 42px;
    text-align: center;
    margin: 6px;
    position: relative;
    z-index: 2;
    cursor: pointer;
    font-size: 2em;
    border-radius: 6px;
}
.td-clock .td-time span.on {
    color: #1977CC;
}
.td-n {
    -webkit-transition: all 0.4s cubic-bezier(.7, 0, .175, 1) 0s;
    -moz-transition: all 0.4s cubic-bezier(.7, 0, .175, 1) 0s;
    -ms-transition: all 0.4s cubic-bezier(.7, 0, .175, 1) 0s;
    transition: all 0.4s cubic-bezier(.7, 0, .175, 1) 0s;
}
.td-n2 {
    -webkit-transition: all 0.2s linear 0s;
    -moz-transition: all 0.2s linear 0s;
    -ms-transition: all 0.2s linear 0s;
    transition: all 0.2s linear 0s;
}
